<template>
  <div class="details">
    <j-header
      title=""
      icon="hold"
      :icon-router="{ router: 'hold', params }"
      :go="false"
      @goback="goback">
      <div class="switch">
        <i class="arrow-left wave" @click="tab('left')">
        </i>{{ navTitle }}<i class="arrow-right wave" @click="tab('right')"></i>
      </div>
    </j-header>
    <nav :style="{ background: navData.sorted}">
      <p><span>{{ navData.price }}</span></p>
      <ul>
        <li><span>今日：{{ navData.start }}</span></li>
        <li><span>最高：{{ navData.max }}</span></li>
        <li><span>昨收：{{ navData.end }}</span></li>
        <li><span>最低：{{ navData.min }}</span></li>
      </ul>
    </nav>
    <main>
      <div id="charts-container">
        <div id="charts"></div>
        <div v-show="pointShow" class="moving-point">
          <span></span>
        </div>
      </div>
      <ul class="nav">
        <li
        class="wave"
        v-for="(obj, index) in timesData"
        :key='index'
        @click="changeTimes(index)"
        :class="[obj.active? 'active' : '']">
        <span>{{obj.title}}</span>
      </li>
      </ul>
      <ul class="money">
        <li>
          <p><button @click="jumpHold">持仓</button></p>
          <p class="column">
            <span>0</span>
            <span>赢家券(￥)</span>
          </p>
        </li>
        <li>
          <p class="column">
            <span>{{ orderData.balance }}</span>
            <span>用户净值(￥)</span>
          </p>
          <p><button @click="jumpPay">充值</button></p>
        </li>
      </ul>
      <div class="bar">
        <div class="bar1">
          <div class="left-bar">
            <div></div>
          </div>
          <p>{{ bar.rise }}%</p>
        </div>
        <p>买卖量</p>
        <div class="bar2">
          <p>{{ bar.fall }}%</p>
          <div class="right-bar">
            <div></div>
          </div>
        </div>
      </div>
      <div class="buy">
        <button @click="showOrderBtn('买涨')">买涨</button>
        <button @click="showOrderBtn('买跌')">买跌</button>
      </div>
    </main>
    <orders v-model="showOrder" :order-data="orderData" @showCount="showCount"/>
    <account v-model="showAccount" :order-data="orderData" :account-data="accountData"/>
  </div>
</template>

<script>
// @ is an alias to /src
import Highcharts from 'highcharts/highstock'
import JHeader from '@/components/JHeader'
import lineOption from './lineOption'
import KlineOption from './KlineOption'
import orders from './components/orders'
import account from './components/account'
import { getUid } from '@/utils/auth'
// import load from './lineLoad'
import { parseTime } from '@/utils'

export default {
  name: 'j-details',
  components: {
    JHeader,
    orders,
    account
  },
  data () {
    return {
      lineOption: lineOption(this),
      KlineOption: KlineOption(this),
      pointShow: true,
      showOrder: false,
      showAccount: false,
      lastTime: '',
      lineTimer: '',
      navTitle: '加载中',
      KlineTooltipDate: [],
      currentMold: '',
      tabData: [],
      bar: {
        fall: 50,
        rise: 50
      },
      orderData: {
        price: '加载中',
        productName: '加载中',
        sorted: '#f45252',
        orderStatus: '加载中',
        balance: '加载中',
        productEname: '加载中',
        productId: ''
      },
      accountData: {},
      navData: {
        end: '加载中',
        max: '加载中',
        min: '加载中',
        start: '加载中',
        price: '加载中',
        sorted: '#f45252'
      },
      timesData: [
        {
          title: '分时',
          active: 1,
          mold: ''
        },
        {
          title: '1分钟',
          active: 0,
          mold: '_1'
        },
        {
          title: '5分钟',
          active: 0,
          mold: '_5'
        },
        {
          title: '15分钟',
          active: 0,
          mold: '_15'
        },
        {
          title: '30分钟',
          active: 0,
          mold: '_30'
        }
      ],
      params: {}
    }
  },
  created () {
    this.params = this.$route.params
    this.init('')
  },
  methods: {
    init (mold) {
      this.navData.price = this.params.price
      this.navTitle = this.params.cname
      this.orderData.productName = this.params.cname
      this.orderData.productEname = this.params.ename
      this.orderData.price = this.params.price
      this.orderData.productId = this.params.id
      this.request({
        url: `/index/${this.params.id}`,
        method: 'get',
        params: { type: this.params.name + mold }
      }).then(res => {
        if (res.code === 1) {
          // 头部信息
          for (var key in res.data) {
            if (this.navData[key]) {
              this.navData[key] = res.data[key]
            }
          }
          // 最后一个时间
          this.lastTime = res.data.data[res.data.data.length - 1][0]
          // X时间轴和Y轴赋值
          this.lineOption.xAxis.categories = []
          this.lineOption.series[0].data = []
          if (mold) {
            this.KlineOption.xAxis.categories = []
            this.KlineOption.series[0].data = []
            this.KlineOption.series[1].data = this.calculateMA(5, res.data.data)
            this.KlineOption.series[2].data = this.calculateMA(10, res.data.data)
            this.KlineOption.series[3].data = this.calculateMA(20, res.data.data)
          }
          res.data.data.forEach(arry => {
            if (!mold) {
              this.lineOption.xAxis.categories.push(parseTime(arry[0], '{h}:{i}'))
              this.lineOption.series[0].data.push(arry[1])
            } else {
              this.KlineOption.xAxis.categories.push(parseTime(arry[0], '{h}:{i}'))
              this.KlineTooltipDate.push(parseTime(arry[0], '{m}-{d} {h}:{i}'))
              this.KlineOption.series[0].data.push([arry[1], arry[2], arry[3], arry[4]])
            }
          })
          Highcharts.setOptions({ global: { useUTC: false } })
          Highcharts.chart('charts', mold ? this.KlineOption : this.lineOption)
        }
      })
      this.request({
        url: '/index',
        method: 'get'
      }).then(res => {
        if (res.code === 1) {
          this.tabData = res.data
        }
      })
      // 获取用户余额
      this.request({
        url: `/user/${getUid()}`,
        method: 'get'
      }).then(res => {
        if (res.data.salesman * 1 === 2) {
          this.orderData.balance = res.data.balance * 1
        } else if (res.data.salesman * 1 === 10) {
          this.orderData.balance = res.data.wallet * 1
        }
      })
    },
    tab (status) {
      var index = null
      this.tabData.forEach((o, i) => {
        if (o.cname === this.navTitle) {
          index = i
        }
      })
      if (status === 'left') {
        this.params = index > 0 ? this.tabData[index - 1] : this.tabData[0]
        this.init(this.currentMold)
      } else {
        this.params = index + 1 === this.tabData.length ? this.tabData[this.tabData.length - 1] : this.tabData[index + 1]
        this.init(this.currentMold)
      }
    },
    calculateMA (dayCount, data) {
      var result = []
      for (var i = 0, len = data.length; i < len; i++) {
        if (i < dayCount) {
          continue
        }
        var sum = 0
        for (var j = 0; j < dayCount; j++) {
          sum += data[i - j][4]
        }
        result.push(sum / dayCount)
      }
      return result
    },
    changeTimes (index) {
      this.timesData.forEach(o => {
        o.active = 0
      })
      this.timesData[index].active = 1
      this.currentMold = this.timesData[index].mold
      this.init(this.timesData[index].mold)
    },
    // 买涨买跌
    showOrderBtn (val) {
      this.showOrder = true
      this.orderData.orderStatus = val
    },
    // 显示倒计时
    showCount (params) {
      this.accountData = params
      this.showAccount = true
    },
    goback () {
      this.$router.push('/home/homepage')
    },
    jumpPay () {
      this.$router.replace({ name: 'recharge' })
    },
    jumpHold () {
      this.$router.push({ name: 'hold', params: this.params })
    }
  },
  mounted () {
  },
  destroyed () {
    clearInterval(this.lineTimer)
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
