<template>
  <div class="shade" v-show="show">
    <div class="order">
      <h1>订单确认<i class="wave" @click="close">×</i></h1>
      <div class="order-main">
        <div class="time">
          <h2>到期时间</h2>
          <ul class="list">
            <li
              v-for="obj in timeList"
              :key="obj.time"
              class="wave"
              :style="{ background: `url(${require('@/assets/order/'+obj.time+obj.status+'.png')}) no-repeat` }"
              @click="timeChoose(obj)">
            </li>
          </ul>
        </div>
        <div class="investment">
          <h2>选择投资金额(元)</h2>
          <ul class="list">
            <li
              v-for="obj in investment"
              :key="obj.money"
              class="wave"
              @click="investmentChoose(obj)">
              <span :class="[obj.active ? 'active' : '']">￥{{ obj.money }}</span>
            </li>
          </ul>
        </div>
        <div class="custom">
          <h2>自定义金额</h2>
          <div class="count">
            <j-number v-model="money" :min="orderInfo.ordersMin * 1" :max="orderInfo.ordersMax * 1"></j-number>
          </div>
        </div>
        <table class="order-info">
          <thead>
            <tr>
              <td>名称</td>
              <td>方向</td>
              <td>现价</td>
              <td>金额</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{{ orderData.productName }}</td>
              <td :style="{ color: orderData.orderStatus === '买跌' ? '#16a731' : '#f45252'}">{{ orderData.orderStatus }}</td>
              <td :style="{ color: orderData.sorted }">{{ orderData.price }}</td>
              <td>￥{{ money }}</td>
            </tr>
            <tr class="service-charge">
              <td>手续费<i>({{ orderInfo.ordersFee * 100 }}%/单)</i></td>
              <td>￥{{ (money * orderInfo.ordersFee).toFixed(2) }}</td>
            </tr>
          </tbody>
        </table>
        <p class="balance">余额￥<span>{{ (orderData.balance - money).toFixed(2) }}</span></p>
        <button @click="countersign">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import JNumber from '@/components/JNumber'

export default {
  name: 'order',
  components: {
    JNumber
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: true
    },
    orderData: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      timeList: [
        {
          time: 30,
          status: 'active'
        },
        {
          time: 60,
          status: 'default'
        },
        {
          time: 90,
          status: 'default'
        }
      ],
      investment: [
        {
          money: 100,
          active: 1
        },
        {
          money: 300,
          active: 0
        },
        {
          money: 500,
          active: 0
        },
        {
          money: 1000,
          active: 0
        },
        {
          money: 3000,
          active: 0
        },
        {
          money: 5000,
          active: 0
        }
      ],
      orderInfo: {
        ordersFee: '加载中',
        ordersMin: '',
        ordersMax: ''
      },
      money: 100,
      second: 30
    }
  },
  created () {
    this.request({
      url: '/config',
      method: 'get'
    }).then(res => {
      res.data.forEach(e => {
        if (e.name === 'product_price') {
          this.investment = e.value.split(',').map(item => {
            return { money: item, active: 0 }
          })
        } else if (e.name === 'orders_fee') {
          this.orderInfo.ordersFee = e.value.split(',')[0]
        } else if (e.name === 'orders_min') {
          this.orderInfo.ordersMin = e.value
        } else if (e.name === 'orders_max') {
          this.orderInfo.ordersMax = e.value
        }
      })
    })
  },
  methods: {
    timeChoose (params) {
      this.timeList.forEach(o => {
        o.status = 'default'
      })
      params.status = 'active'
      this.second = params.time
    },
    investmentChoose (params) {
      this.investment.forEach(o => {
        o.active = 0
      })
      params.active = 1
      this.money = params.money
    },
    close () {
      this.$emit('change', false)
    },
    async countersign () {
      if (this.orderData.balance < this.money) {
        this.$toast('余额不足，请充值!')
        return false
      }
      let res = await this.request({
        url: '/orders',
        method: 'post',
        data: {
          time: this.second, // 时长
          product_id: this.orderData.productId, // 产品ID
          orderamount: this.money,
          state: this.orderData.orderStatus === '买涨' ? 0 : 1
        }
      })
      if (res.code === 1) {
        this.$emit('showCount', res.data)
      } else {
        this.$toast(res.message)
        return false
      }
      this.$emit('change', false)
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
