<template>
  <div class="account-shade" v-show="show">
    <div class="account">
      <nav>
        <p>
          <span>{{ orderData.productName }}</span>
          <span>{{ orderData.productEname }}</span>
        </p>
        <i class="wave" @click="close">×</i>
      </nav>
      <div class="order-main">
        <div class="count" v-show="!showResult">
          <h2>倒计时</h2>
          <div class="circle-box">
            <j-circle
            :percent="percent"
            :stroke-width="10"
            :trail-width="10"
            anticlockwise>
            <span>{{ time > 0 ? `${time}秒` : '请稍后' }}</span>
          </j-circle>
          </div>
        </div>
        <ul class="results" v-show="showResult">
          <li>交易结果</li>
          <li v-if="status === 1" style="background: #f12929;">盈</li>
          <li v-if="status === 2" style="background: #269c02;">亏</li>
          <li v-if="status === 3" style="background: #3b95f4;">平</li>
          <li v-if="status === 1" style="color: #f12929;">+{{money}}</li>
          <li v-if="status === 2" style="color: #269c02;">-{{money}}</li>
          <li v-if="status === 3" style="color: #3b95f4;">{{money}}</li>
          <li>{{ status === 1 ? '继续交易 再接再厉~' : '很遗憾，期待下次与您相遇~' }}</li>
        </ul>
        <table class="order-info">
          <thead>
            <tr>
              <td>方向</td>
              <td>金额 (元)</td>
              <td>执行价</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td :style="{ color: orderData.orderStatus === '买跌' ? '#16a731' : '#f45252'}">{{ orderData.orderStatus }}</td>
              <td :style="{ color: '#e18000' }">{{ accountData.amount }}</td>
              <td>{{ accountData.price }}</td>
            </tr>
          </tbody>
        </table>
        <button @click="close">继续交易</button>
      </div>
    </div>
    <j-audio :src="require('@/assets/mp3/gold.mp3')" v-model="audio"/>
  </div>
</template>

<script>
// @ is an alias to /src
import JCircle from '@/components/JCircle'
import JAudio from '@/components/JAudio'

export default {
  name: 'account',
  components: {
    JCircle,
    JAudio
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: true
    },
    orderData: {
      type: Object,
      required: true
    },
    accountData: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      showResult: false,
      money: 100,
      status: 1,
      percent: 0,
      timer: '',
      time: null,
      resultTimer: null,
      audio: {}
    }
  },
  created () {
  },
  watch: {
    accountData (params) {
      if (params.hasOwnProperty('time')) {
        this.count(params)
      }
    }
  },
  methods: {
    close () {
      this.$emit('change', false)
      clearInterval(this.timer)
      this.showResult = false
      this.percent = 0
      this.time = 0
    },
    count (params) {
      this.time = params.time
      this.timer = setInterval(() => {
        this.time--
        this.percent = (1 - this.time / params.time) * 100
        if (this.time < 1) {
          clearInterval(this.timer)
          this.result()
        }
      }, 1000)
    },
    async result () {
      let res = await this.request({ url: `/orders/${this.accountData.id}` })
      if (res.code === 1) {
        if (res.data.endstatus * 1 === 0) {
          clearInterval(this.resultTimer)
          this.resultTimer = setInterval(() => {
            this.result()
          }, 3000)
        } else {
          clearInterval(this.resultTimer)
          this.showResult = true
          this.money = res.data.winmoney
          this.status = res.data.endstatus * 1
          res.data.endstatus * 1 === 1 && this.audio.play()
        }
      }
    }
  },
  mounted () {
    // 监听用户是否切换app到后台，切换到后台，js失效
    // document.addEventListener("visibilitychange", function(){
    //   // document.title = document.hidden ? "用户离开了" : "用户回来了"
    //   // that.ifDownTimSucc = false;
    //   if (!document.hidden) {
    //     clearInterval(this.timer)
    //     this.$emit('change', false)
    //   }
    // })
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
