<template>
    <div class="jun-number">
        <button
          @click="arithmetic('-')"
          @touchstart="gtouchstart('-', $event)"
          @touchmove="gtouchmove()"
          @touchend="gtouchend"
          class="wave">-</button>
        <input type="number" v-model="NewNumber" @change="monitor">
        <button
          @click="arithmetic('+')"
          @touchstart="gtouchstart('+', $event)"
          @touchmove="gtouchmove()"
          @touchend="gtouchend"
          class="wave">+</button>
        <!-- <svg-icon :icon-class="icon" v-show="show" class="wave"/> -->
    </div>
</template>

<script>

export default {
  name: 'jun-number',
  data () {
    return {
      timeOutEvent: '',
      speed: 60
    }
  },
  props: ['value', 'min', 'max'],
  computed: {
    NewNumber: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  created () {
  },
  methods: {
    arithmetic (status) {
      if (status === '-') {
        if (this.NewNumber > this.min) {
          this.NewNumber--
        } else {
          this.NewNumber = this.min
        }
      } else {
        if (this.NewNumber < this.max) {
          this.NewNumber++
        } else {
          this.NewNumber = this.max
        }
      }
      this.$emit('change', this.NewNumber)
    },
    gtouchstart (val, e) {
      // 阻止长按选中
      // e.target.onselectstart = (e) => {
      //   return false
      // }
      // 阻止浏览器默认菜单
      // e.preventDefault()
      clearInterval(this.timeOutEvent)
      this.timeOutEvent = setInterval(() => {
        this.speed--
        if (this.speed < 1) this.speed = 1
        this.arithmetic(val)
        this.gtouchstart(val, e)
      }, this.speed)
    },
    gtouchend () {
      this.speed = 60
      clearInterval(this.timeOutEvent)
    },
    gtouchmove () {
      clearInterval(this.timeOutEvent)
    },
    monitor () {
      this.NewNumber = this.NewNumber >= this.min ? this.NewNumber > this.max ? this.max : this.NewNumber : this.min
    }
  }
}
</script>

<style scope lang="scss">
  @import '@/style/mixin.scss';
  .jun-number {
    @include fb(center-row);
    button {
      @include wh(0.66rem);
      @include br(0.1rem);
      background: none;
      font-size: 0.4rem;
      border: 1px solid #949494;
    }
    input {
      @include wh(2.2rem, .6rem);
      // border-bottom: .01rem solid #949494;
      // background: none;
      text-align: center;
    }
  }
</style>
