import { parseTime, animate } from '@/utils'

function lastData (that) {
  const position = that.series[0].data[that.series[0].data.length - 1]
  const point = document.querySelector('.moving-point')
  // point.style.top = position.plotY + 8 + 'px'
  // point.style.left = position.plotX + 8 + 'px'
  animate(point, {
    top: position.plotY + 9,
    left: position.plotX + 8
  })
}

function bar (params) {
  // const width = document.querySelector('.left-bar').clientWidth
  // const leftBar = document.querySelector('.left-bar div')
  // const rightBar = document.querySelector('.right-bar div')
  // animate(leftBar, {
  //   width: params.rise / 100 * width
  // }, 60)
  // animate(rightBar, {
  //   width: params.fall / 100 * width
  // }, 60)
  const leftBar = document.querySelector('.left-bar div')
  const rightBar = document.querySelector('.right-bar div')
  leftBar.style.transform = `translateX(+${100 - params.rise}%)`
  rightBar.style.transform = `translateX(-${100 - params.fall}%)`
}

export default function lineOption (Vue) {
  return {
    chart: {
      type: 'areaspline',
      backgroundColor: 'none',
      resetZoomButton: false,
      pinchType: 'none',
      zoomType: 'none',
      panning: false,
      events: {
        load () {
          lastData(this)
          Vue.pointShow = true
          clearInterval(Vue.lineTimer)
          Vue.lineTimer = setInterval(() => {
            Vue.request({
              url: `/index/${Vue.params.id}`,
              method: 'get',
              params: {
                type: Vue.params.name,
                last: 0
              }
            }).then(res => {
              if (parseTime(res.data.time, '{i}') === parseTime(Vue.lastTime, '{i}')) {
                this.series[0].data[this.series[0].data.length - 1].update(res.data.price)
              } else {
                Vue.lastTime = res.data.time
                var categories = Vue.lineOption.xAxis.categories
                var data = Vue.lineOption.series[0].data
                categories.shift()
                data.shift()
                categories.push(parseTime(res.data.time, '{h}:{i}'))
                data.push(res.data.price)
                this.series[0].update({
                  categories, // x轴
                  data // y轴
                })
              }
              lastData(this)
              // 头部信息
              for (var key in res.data) {
                if (Vue.navData[key]) {
                  Vue.navData[key] = res.data[key]
                }
              }
              // 订单信息
              Vue.orderData.price = res.data.price
              Vue.orderData.sorted = res.data.sorted
              // 买卖量
              bar(res.data)
              Vue.bar = res.data
            })
          }, 3000)
        }
      }
    },
    rangeSelector: {
      buttons: [{
        type: 'hour',
        count: 2
      }],
      buttonTheme: {
        style: {
          display: 'none'
        }
      },
      inputStyle: {
        display: 'none'
      },
      labelStyle: {
        display: 'none'
      },
      selected: 0
    },
    // 提示框
    tooltip: {
      formatter: function () {
        return '<b>' + this.x + '</b><br/><b>' + this.series.name + '：</b>' + Number(this.y.toFixed(2))
      },
      style: {
        width: 150,
        height: 150,
        padding: 500,
        fontWeight: 'bold',
        fontSize: '14px',
        color: '#fff'
      },
      followPointer: true,
      followTouchMove: true,
      borderColor: '#ff3d3d',
      borderWidth: 2,
      backgroundColor: {
        linearGradient: [0, 0, 0, 60],
        stops: [
          [0, '#f9c8ce'],
          [1, '#ff3d3d']
        ]
      },
      borderRadius: 20
      // borderColor: '#f00'
    },
    // 标题
    title: {
      text: ''
    },
    // 标题注释
    subtitle: {
      text: ''
    },
    yAxis: {
      // y轴标注
      title: {
        text: ''
      },
      labels: {
        align: 'right',
        x: 0,
        style: {
          // color: '#4a4a4a',
          color: '#333333'
          // fontSize: '16px'
        }
      },
      crosshair: false,
      startOnTick: false,
      // 网格线
      gridLineWidth: 0,
      gridLineColor: '#e6e6e6',
      opposite: true,
      tickPixelInterval: 200,
      plotLines: [{
        value: 0,
        width: 1,
        color: '#808080'
      }]
    },
    xAxis: {
      type: 'datetime',
      gridLineWidth: 0,
      categories: [],
      tickColor: '#cccccc',
      labels: {
        align: 'left',
        step: 10,
        style: {
          color: '#4a4a4a',
          fontSize: '14px'
        }
      },
      // lineColor: '#999',
      lineColor: '#4a4a4a',
      crosshair: {
        width: 2,
        color: '#fff'
      },
      dateTimeLabelFormats: {
        hour: '%H:%M'
      }
    },
    legend: {
      enabled: false
    },
    exporting: {
      enabled: false
    },
    navigator: {
      enabled: false,
      margin: 0,
      height: 0
    },
    credits: {
      enabled: false
    },
    scrollbar: {
      enabled: false
    },
    plotOptions: {
      series: {
        fillColor: {
          linearGradient: {
            x1: 0,
            x2: 0,
            y1: 0,
            y2: 1
          },
          stops: [
            [0, '#f9c3c9'],
            [1, 'rgba(255, 255, 255, 0.3)']
            // [0, 'rgba(44,146,145,10)'],
            // [1, 'rgba(144,146,145,0)'],
            // [0, 'rgba(44,146,145,10)'],
            // [1, 'rgba(44,146,145,0)'],
          ]
        }
      },
      area: {
        pointStart: 1940,
        marker: {
          enabled: false,
          symbol: 'circle',
          radius: 2,
          states: {
            hover: {
              enabled: true
            }
          }
        }
      }
    },
    series: [{
      name: '分时图',
      animation: true,
      lineWidth: 1,
      lineColor: '#ef4b4f',
      threshold: null,
      // 线段打点
      marker: {
        enabled: false
      },
      tooltip: {
        valueDecimals: 2
      },
      data: []
    }]
  }
}
